<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <template>
        <v-flex md12>
          <v-stepper v-model="e1">
            <v-stepper-header>
              <v-stepper-step
                :complete="e1 > 1"
                step="1">Selecione as datas de vencimento e pagamento</v-stepper-step>

              <v-divider />

              <v-stepper-step
                :complete="e1 > 2"
                step="2">Selecione as empresas para repasse</v-stepper-step>

              <v-divider />

              <v-stepper-step
                :complete="e1 > 3"
                step="3">Pagamento do repasse</v-stepper-step>
            </v-stepper-header>

            <v-stepper-items>
              <v-stepper-content step="1">
                <calculo-geral-passo1
                  :value="form"
                  :repasses="repasses"
                  :next-step="continuar"
                  :set-data="setData"
                  :cancelar="cancelar"
                />
              </v-stepper-content>

              <v-stepper-content step="2">
                <template v-if="e1 === 2">
                  <calculo-geral-passo2
                    :value="form"
                    :next-step="continuar"
                    :set-data="setData"
                    :go-back="voltar"
                  />
                </template>
              </v-stepper-content>

              <v-stepper-content step="3">
                <template v-if="e1 === 3">
                  <calculo-geral-passo3
                    :value="form"
                    :disabled="disabled"
                    :next-step="continuar"
                    :go-back="voltar"
                  />
                </template>
              </v-stepper-content>
            </v-stepper-items>
          </v-stepper>
        </v-flex>
      </template>
    </v-layout>
  </v-container>
</template>
<script>
import { TiposBusiness } from '../../../business'
import dateUtils from '../../../utils/dateUtils'

const CalculoGeralPasso1 = () => import('./CalculoGeralPasso1')
const CalculoGeralPasso2 = () => import('./CalculoGeralPasso2')
const CalculoGeralPasso3 = () => import('./CalculoGeralPasso3')

export default {
  metaInfo: {
    titleTemplate: '%s - Repasse Geral'
  },
  components: {
    CalculoGeralPasso1,
    CalculoGeralPasso2,
    CalculoGeralPasso3
  },
  data () {
    return {
      repasses: [],
      form: {
        redeId: null,
        empresaId: null,
        lojaId: null,
        redeNome: null,
        empresaNome: null,
        lojaNome: null,
        peComissaoCompra: 0.00,
        vencimentoAte: dateUtils.currentDate(),
        pagamentoEm: dateUtils.currentDate(),
        flgSaldosZerados: true,
        tpRepasseId: 'INDIFERENTE',
        lojasSelecionadas: [],
        extratoRepasse: {
          vlTotalPagar: 0,
          vlTotalCompras: 0,
          vlTotalComissaoCompras: 0,
          vlTotalCreditoAdmin: 0,
          vlTotalCreditoLojista: 0,
          vlTotalPagamentosLoja: 0
        }
      },
      e1: 0,
      disabled: false
    }
  },
  beforeMount () {
    TiposBusiness.listRepasse().then(response => { this.repasses = response.data })
  },
  methods: {
    validateBeforeSubmit () {
      if (this.$refs.form.validate()) {
        this.continuar()
      } else {
        this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
      }
    },
    continuar () {
      this.e1 < 4 ? this.e1++ : (this.e1 = 1)
    },
    voltar () {
      this.e1 > 1 ? this.e1-- : (this.e1 = 1)
    },
    setData (data) {
      let lojaId = this.form.lojaId
      this.form = data
      if (lojaId) {
        this.form.lojaId = lojaId
      }
      this.formatDatas()
    },
    formatDatas () {
      if (this.form.dataEmissao) {
        this.form.dataEmissao = this.$moment(this.form.dataEmissao).format('YYYY-MM-DD')
      }
      if (this.form.dataNascimento) {
        this.form.dataNascimento = this.$moment(this.form.dataNascimento).format('YYYY-MM-DD')
      }
      if (this.form.observacoes) {
        this.form.observacoes = this.form.observacoes.map(value => {
          value.dataCadastro = this.$moment(value.dataCadastro).format('YYYY-MM-DD')
          return value
        })
      }
    },
    cancelar () {
      // this.$router.push(`/lista-cliente`)
    }
  }
}
</script>
